.wrapper--branding {
  background-color: #494f57;
  border-top: 5px solid #e87411;
  min-height: 75px; }
  .wrapper--branding .site-name {
    display: none; }

.l-region--branding h2 {
  margin: 0; }

.l-region--branding .homepage-tab-form-message {
  display: none; }

.site-logo {
  clear: both;
  height: 50px;
  line-height: 21px;
  margin-left: -0.7em; }
  .site-logo:before {
    /* create a full-height inline block pseudo=element */
    content: " ";
    display: inline-block;
    height: 100%;
    vertical-align: middle;
    /* vertical alignment of the inline element */ }
  .site-logo img {
    display: inline-block;
    margin: 0 auto;
    padding: 0 1em;
    vertical-align: middle; }
    @media (min-width: 70em) {
      .site-logo img {
        margin: 0;
        padding: 0; } }

.svg .site-logo img {
  display: none; }

.svg .site-logo {
  background: url("/sites/all/themes/rbsc/assets/public/images/rbsc_logo.svg") center center no-repeat;
  background-size: contain;
  display: block; }
  @media (min-width: 44em) {
    .svg .site-logo {
      margin-left: 0; } }

.front h1 {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden; }

.l-region--branding .block--allsearch-block-allsearch-search {
  float: right; }
